<template>
  <div class="m-auto">
    <div class="uppy-target">
    </div>
    <div class="uppy uppy-drag-drop"></div>
    <div class="uppy-progress"></div>
  </div>
</template>

<script>
import { Core, Dashboard, DragDrop, Tus, ProgressBar } from 'uppy'

const uppy = new Core({ debug: true })

export default {
  name: 'uppy-loader',

  data: () => ({
    bytesUploaded: 0,
    bytesTotal: 0,
    showProgress: false
  }),

  mounted () {
    let this_ = this

    uppy
      .use(Dashboard, {
        id: 'uppy-uploader-1',
        target: '.uppy-target'
      })
      .use(DragDrop, { target: '.uppy-drag-drop' })
      .use(Tus, { endpoint: '//master.tus.io/files/' })
      .use(ProgressBar, { target: '.uppy-progress' })
      .run()

    uppy.on('core:upload-progress', (data) => {
      this_.$emit('core:upload-progress', data)
      this_.bytesUploaded = data.bytesUploaded
      // console.log(data.id, data.bytesUploaded, data.bytesTotal)
    })

    uppy.on('core:upload-success', (fileId, payload) => {
      let data = {
        fileId: fileId,
        data: payload
      }
      this_.$emit('core:upload-success', data)
    })

    uppy.on('core:success', (fileCount) => {
      this_.$emit('core:success', fileCount)
      this_.showProgress = false
    })
  }
}
</script>

